import { useState } from "react";
import { Link } from "react-router-dom";
import { useWeb3 } from "@/contexts/Web3Context";

const Navbar = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  const { connect, connecting, isConnected, account } = useWeb3();

  // Add scroll listener
  window.addEventListener("scroll", () => {
    if (window.scrollY > 20) {
      setIsScrolled(true);
    } else {
      setIsScrolled(false);
    }
  });

  return (
    <nav
      className={`fixed top-0 left-0 right-0 z-50 transition-all duration-300 ${
        isScrolled ? "glass-panel py-4" : "py-6"
      }`}
    >
      <div className="container mx-auto px-4">
        <div className="flex items-center justify-between">
          <Link
            to="/"
            className="text-2xl font-medium tracking-tight text-luxury-black hover:text-luxury-gold transition-colors"
          >
            LUXE VERIFY
          </Link>
          <div className="flex items-center space-x-8">
            <Link
              to="/buyers"
              className="text-sm font-medium text-luxury-black hover:text-luxury-gold transition-colors"
            >
              FOR BUYERS
            </Link>
            <Link
              to="/sellers"
              className="text-sm font-medium text-luxury-black hover:text-luxury-gold transition-colors"
            >
              FOR SELLERS
            </Link>
            <button
              onClick={connect}
              disabled={connecting || isConnected}
              className="px-6 py-2 bg-luxury-black text-white text-sm font-medium rounded-full hover:bg-opacity-90 transition-all disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {connecting
                ? "Connecting..."
                : isConnected
                ? `Connected: ${account?.slice(0, 6)}...${account?.slice(-4)}`
                : "Connect Wallet"}
            </button>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
